body {
  margin: 0;
  padding: 0;
  background: #447099;
  overflow-x: hidden;
}

.clickable:hover {
  background: #007199;
  color: white;
  transition: 500ms;
}

.header {
  color: azure;
  display: flex;
  background: cornflowerblue;
  width: 100%;
  height: 100px;
  justify-content: space-between;
  margin-bottom: 10px
}

.log-out {
  position: absolute;
  right: 0
}
.f-s-5em {
  font-size: 5em
}

.f-s-20px {
  font-size: 20px
}

.h-40 {
  height: 40%;
}

.overflow {
  overflow-y: scroll;
  overflow-x: hidden;
}








.scroller::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border-radius: 5px;
  background-color: azure;
}

.scroller::-webkit-scrollbar {
  width: 20px
}

.scroller::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: cadetblue;
}

.scrollbar {
  padding: 10px;
  overflow-y: scroll;
}
.h-500px {
  height: 500px
}

.share-menu {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.share-list {
  /*cornflowerblue but in rgba for saving opacity*/
  background: rgba(100, 149, 237, 1)
}
.header-text {
  display: flex;
  padding: 0 10px
}
.b-1px-s-azure {
  border: 1px solid azure
}

.b-b-1px-s-azure {
  border-bottom: 1px solid azure
}

.lds-facebook {
  position: fixed;
  top:35%;
  right: 55.2%;
}
.lds-facebook div {
  display: flex;
  position: absolute;
  width: 20px;
  background: #fff;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 30px;
  animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 60px;
  animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 90px;
  animation-delay: 0;
}
@keyframes lds-facebook {
  0% {
    top: 12px;
    height: 102px;
  }
  50%, 100% {
    top: 38px;
    height: 52px;
  }
}