.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.button {
    color: azure;
    background: cornflowerblue;
    border: 1px solid cornflowerblue;
}

.link {
    text-decoration: none;
    border: none;
    color: azure
}

.button:hover {
    color: cornflowerblue;
    background: azure;
    transition: 500ms;
}

.link:hover {
    color: cornflowerblue;
    transition: 500ms;
}

