.isa_success, .isa_error {
    padding:12px;
    width: 100%;

}

.isa_success {
    color: #4F8A10;
    background-color: #DFF2BF;
}

.isa_error {
    color: #D8000C;
    background-color: #FFD2D2;
}

.isa_success i, .isa_error i {
    margin:10px 22px;
    font-size:3.5em;
    vertical-align:middle;
}

.logMsg {
    position: fixed;
    top: -20%;
    animation-fill-mode: forwards;
    animation-name: showMsg;
    animation-duration: 500ms;
}

@keyframes showMsg{
    from {
        top: -20%
    }

    to {
        top: 0
    }
}

@keyframes hideMsg{
    from {
        top: 0
    }

    to {
        top: -20%
    }
}

.w-b {
    word-break: break-all
}